import React, { FC } from 'react';
import { Box, Link } from '@material-ui/core';

// Required when using free Mapbox stuff such as background tiles. Docs:
// docs.mapbox.com/help/how-mapbox-works/attribution/#other-mapping-frameworks
export const MapboxWordmark: FC = () => {
  const wordMarkLinkStyle = {
    // Mapbox wordmark via data URI
    backgroundImage:
      'url(data:image/svg+xml;base64,)',
    backgroundPosition: 0,
    backgroundRepeat: 'no-repeat',
    backgroundSize: '65px 20px',
    display: 'block',
    height: 20,
    overflow: 'hidden',
    textIndent: -9999,
    width: 65,
  };

  return (
    <Box position="absolute" bottom={5} left={8} zIndex={99999}>
      <Link
        href="http://mapbox.com/about/maps"
        target="_blank"
        style={wordMarkLinkStyle}
      >
        Mapbox
      </Link>
    </Box>
  );
};
